@import url(http://fonts.googleapis.com/css?family=Playfair%20Display);
@import url(http://fonts.googleapis.com/css?family=Nunito%20Sans);

body {
  height: 400px;
  font-family: "Nunito Sans";
  background-color: #FAFAFA;
  padding: 0%;
}

.App {
  text-align: center;
}

main{
  min-height: 400px;
  color: #303131;
  align-content: center;
}

h1{
  font-family: 'Nunito', sans-serif;
  font-size: 45px;
  color: #FAFAFA
}

h2{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 37px;
  color: #9ECDCD
}

p{
  font-size: 20px;
}

.app-links {
  font-size: 25px;
  color: #FDFEFE;
}

nav {
  display: flex;
  padding: 1% 7%;
  background-color: #9ECDCD;
  justify-content: space-between;
  text-align: center;
  align-items: center;
  height: 60px;
  border-bottom: 4px solid #444444;
}

nav > ul > li {
  display: inline-block;
  padding: 10px 15px;
}

nav > ul > li:hover {
  background-color: #567a7a;
  transition: 0.7s;
}

.header-logo{
  width: 300px;
}

.header-image {
  height: 55px;
  width: 55px;
  border-radius: 40%;
  border: 6px solid #FDFEFE; 
}

.page-name{
  width: 550px;
  margin: 10px;
  padding: 10px;
}

.home-article{
  display: table-cell;
  height:80vh;
  width:100vw;
  vertical-align: middle;
}

.home-article h2{
  font-size: 58px;
}

.home-article p{
  font-size: 30px;
}

table {
  font-family: "Nunito Sans";
  border-collapse: collapse;
  margin: auto;
}

caption {
  background-color: #CEECE9;
  border: 1.5px solid #383939;
  border-radius: 3px 3px 0 0;
  color: #383939;
  font-weight: bold;
  padding: 2% 0;
}

thead {
  height: 45px;
}

tbody{
  text-align: center;
}

tr{
  height: 32px;
}

tr td {
  border: 2px solid #9ECDCD;
  padding: 1%;
  min-width: 150px;
}

tr th {
  border: 4px solid #9ECDCD;
  background-color: #DBE5E5;
  padding: 1%;
  min-width: 100px;
}

th td {
  border: 1px solid #383939
}

tr:nth-child(odd) td {background-color:#CEECE9;}

.qv {
  padding: 5%;
}


form{
  font-family: "Nunito Sans";
  padding: 3%;
  width: 600px;
  margin: auto;
}

.alert {
  background-color: rgba(146, 146, 197, 0.267);
}

#footer{
  position: absolute;
  bottom:0;
  width: 100%;
  text-align: center;
}

fieldset{
  font-family: "Nunito Sans";
}

legend{
  font-family: "Nunito Sans";
}

input{
  font-family: "Nunito Sans";
  font-size: 12px;
  text-align: center;
}

button{
  font-family: "Nunito Sans";
}